import React,{useState} from 'react'
import {Header,Logo,LogoBox,Inside,Element,Nav,MobileIcon} from './navelements'
import './nav.css'
import 'tachyons'
import {VscThreeBars} from 'react-icons/vsc'

const Navigation = ({sidebarTrigger}) => {

const [trigger,setNav]=useState(false)

const change=()=>{
  
    if(window.scrollY>=200){
        setNav(true)
    }
    else{
        setNav(false)
    }

}

window.addEventListener('scroll',change)

    return (
        <>
        <Header className={trigger?"header active": "header"}>
            <LogoBox>
                <Logo trigger={trigger}className={trigger?"logo active": "logo"}>
            ifbi</Logo>

            </LogoBox>


         <MobileIcon onClick={sidebarTrigger} trigger={trigger} >
             <VscThreeBars/>
         </MobileIcon>
            <Inside>
                <Element>
                    <Nav className="nav" trigger={trigger}>About</Nav>
                </Element>
                <Element>
                    <Nav className="nav" trigger={trigger}>Content</Nav>
                </Element>
                <Element>
                    <Nav className="nav" trigger={trigger}>TSI</Nav>
                </Element>


            </Inside>

        </Header>
            
        </>
    )
}

export default Navigation
