import React ,{useState} from 'react'
import './sidebar.css'
import { Sidebarcontainer,Inside,Links} from './sidebarelements'
const Sidebar = ({sidebarTrigger,SidebarOpen}) => {

    const [trigger,setNav]=useState(false)

const change=()=>{
  
    if(window.scrollY>=200){
        setNav(true)
    }
    else{
        setNav(false)
    }

}
window.addEventListener('scroll',change)

    return (
        <Sidebarcontainer trigger={trigger} SidebarOpen={SidebarOpen}>

            <Inside className="inside">
                <Links SidebarOpen={SidebarOpen} trigger={trigger}>
                About
                </Links>
                <Links SidebarOpen={SidebarOpen} trigger={trigger} >
                Content
                </Links>
                <Links SidebarOpen={SidebarOpen} trigger={trigger}>
                TSI
                </Links>
                <Links SidebarOpen={SidebarOpen} trigger={trigger}>
                Contact
                </Links>
                <Links SidebarOpen={SidebarOpen} trigger={trigger}>
                QNA
                </Links>


            </Inside>
           

        </Sidebarcontainer>
    )
}

export default Sidebar
